@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


/* Font Style */

h1 {
  font-family: 'Roboto', sans-serif;
  font-weight: 900;
  font-size: 48px;
  margin-top: 45px;
  margin-bottom: 25px;
}

.h1-splash {
  font-size: 120px;
}

.h1-section-header {
  font-size: 58px;
}

h2 {
  font-family: 'Roboto', sans-serif;
}

.h2-splash {
  font-weight: 400;
  color: #858585;
  text-align: center;
}

h3 {
  font-size: 20px;
  font-weight: 400;
  text-transform: uppercase;
  padding-bottom: 125px;
}

h3::before {
  content: "";
  display: inline-block;
  width: 100px;
  border-bottom: 1px solid #000000;
  vertical-align: middle;
  margin-right: 3px;
  text-transform: uppercase;

}

h4 {
  font-size: 24px;
  font-weight: 400;
  position: relative;
  margin-bottom: 30px;
}

.highlight-box {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
}

.highlight {
  z-index: -1;
  width: 100%;
  position: absolute;
  height: 10px;
  border-bottom: solid #E7C82A 10px;
  bottom: -2px;
  /* opacity: 50%; */
}

.highlight {
  opacity: 50%;
}

h5 {
  font-size: 16px;
  font-weight: 900;
}

p {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  line-height: 150.2%;
  font-size: 22px;
}

/* Flex Classes */
.flex {
  display: flex;
}

.center {
  justify-content: center;
  align-items: center;
}

.flex-d-col {
  flex-direction: column;
}

.flex-d-row {
  flex-direction: row;
}

.space-between {
  justify-content: space-between;
}

.center-vertical {
  align-items: center;
}


/* Sections */
section {
  width: 80%;
  /* border: 1px solid hotpink; */
  padding-top: 215px;
  max-width: 1140px;
}

/* Navigation Bar Styling */

.nav {
    width: 100%;
    /* border: 1px green solid; */
    height: 88px;
    /* border-bottom: 1px solid gray; */
    box-shadow: 0 8px 6px -6px #DFDFDF;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: #FFFFFF;
}

.nav-wrapper {
    /* border: 1px solid hotpink; */
    padding: 0px;
}

.menu {
    width: 350px;
    /* border: 1px solid gray; */
}

.nav-item {
    text-decoration: none;
    color: #000000;
}

.nav-item > p {
    font-size: 20px;
    font-weight: 300;
}

.nav-item:hover {
    color: #7A9872;
    /* text-decoration: underline; */
    position: relative;
}

.nav-item:hover::before {
    content:"";
    position: absolute;
    color: #7A9872;
    width: 100%;
    height: 10px;
    bottom: 3px;
    background-color: #7A9872;
    opacity: 50%;
}
.footer, .footer-wrapper {
    /* border: 1px solid green; */
    padding: 0px;
}
.footer {
    height: 288px;
    margin-top: 150px;
    background-color: #EAEAEA;
}

.footer-wrapper {
    margin-top: 60px;
}

img {
    width: 48px;
}

a {
    text-decoration: none;  
    position: relative;
}

.email {
    font-size: 48px;
    font-weight: 900;
    text-decoration: none;
    color: black;
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.email::after {
    content: "";
    position: absolute;
    width: 102%;
    height: 10px;
    border-bottom: solid #E7C82A 20px;
    opacity: 50%;
    bottom: 0px;
    left: 0px;
}

/* Tags */

.project-card {
    width: 49%;
    /* border: 1px solid blue; */
    margin-bottom: 50px;
}

.project-tag-wrapper {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    flex-wrap: wrap;
    margin-bottom: 15px;
}

.project-image {
    width: 100%;
}

.tag {
    background-color: #DFDFDF;
    margin-right: 10px;
    height: 40px;
    /* width: 200px;
    max-width: 200px; */
    /* padding top, right, bottom, left */
    padding: 0px, 15px, 0px, 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    /* border: 1px blue solid; */
    border-radius: 10px;
    margin-bottom: 60px;
}

.h5-tag {
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 15px;
    /* border: 1px hotpink solid; */
}

.project-card:nth-child(even) {
    margin-top: 180px;
}

.link {
    text-decoration: none;
    color: black;
}

.link:hover {
    color: #7A9872;
}

.link::after:hover {
    background-color: #7A9872;
}
